.nav-header {
  position: relative;
  font-size: 12pt;
  font-weight: bold;
  color: var(--col-nav-font);
  padding: 10px 12px;
  min-height: 52px;
  background-color: var(--col-nav-bg);
  border-top: solid 1px var(--col-nav-border);
  z-index: auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
}
.nav-header span:first-of-type {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 8px;
}

.nav-bar {
  float: right;
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.nav-btn {
  margin-left: 6px;
  border-radius: 6px;
  padding: 8px;
  transition: background-color 0.2s ease, transform 0.15s ease;
  cursor: pointer;
}

.nav-btn:hover {
  background-color: var(--col-nav-hover);
  transform: scale(1.05);
}

