.select-container {
  width: 100%;
  position: relative;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", sans-serif;
  user-select: none;
  font-size: 13px;
}

.select-entries {
  margin: 10px 12px 8px;
  background-color: var(--col-table-even-value);
  border: 1px solid var(--col-border);
  border-radius: 8px;
  overflow-y: auto;
  overflow-x: hidden;
  max-width: 245px;
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
}
.select-entries:hover {
  outline: none;
  border-color: var(--col-link);
  box-shadow: 0 0 0 3px rgba(0, 102, 204, 0.1);
}

.select-entry {
  display: flex;
  align-items: center;
  padding: 3px 12px 3px 10px;
  cursor: pointer;
  border-radius: 3px;
  transition: background-color 0.15s ease, transform 0.1s ease;
  gap: 10px;
  color: var(--col-text);
  box-sizing: border-box;
  min-width: 0;
  user-select: none;
}
.select-entry:first-child {
  border-radius: 8px 8px 0 0;
}
.select-entry:last-child {
  border-radius: 0 0 8px 8px;
}
.select-entry:only-child {
  border-radius: 8px;
}
.select-entry:hover {
  background-color: var(--col-table-odd-value);
}
.select-entry:active {
  transform: scale(0.98);
}
.select-entry.selected {
  background-color: var(--col-selected);
  font-weight: 500;
}
.select-entry.selected:hover {
  background-color: var(--col-selected);
  filter: brightness(0.95);
}

.select-icon {
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
}

.select-text {
  flex: 1;
  color: var(--col-text);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  min-width: 0;
}

.select-count {
  color: var(--col-count);
  font-size: 12px;
  margin-left: auto;
  padding-left: 8px;
  flex-shrink: 0;
}

.select-disabled {
  opacity: 0.5;
  pointer-events: none;
}

.select-container.no-selection .select-entry:not(.selected) .select-text,
.select-container.no-selection .select-entry:not(.selected) .select-count {
  color: var(--col-disabled);
  opacity: 0.6;
}

.select-entry.filter-item-off {
  color: var(--col-disabled) !important;
  opacity: 0.5;
}
.select-entry.filter-item-off .select-text,
.select-entry.filter-item-off .select-count {
  color: var(--col-disabled) !important;
}

