#details {
  position: relative;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  max-height: 145px;
  overflow-y: auto;
  overflow-x: hidden;
}

.details-tabs {
  display: inline-flex;
  gap: 4px;
  align-items: center;
}

.details-tab {
  padding: 4px 12px;
  cursor: pointer;
  background-color: var(--col-table-head);
  border: 1px solid var(--col-border);
  border-radius: 3px;
  font-size: 10pt;
  font-weight: normal;
  transition: background-color 0.2s;
  white-space: nowrap;
}
.details-tab:hover {
  background-color: var(--col-table-odd-value);
}
.details-tab.details-tab-active {
  background-color: var(--col-primary);
  color: var(--col-background);
  font-weight: bold;
  border-color: var(--col-primary);
}

.details-tab-content {
  display: none;
}
.details-tab-content.details-tab-content-active {
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

#details-table-basic,
#details-table-details {
  position: relative;
  table-layout: fixed;
  border-collapse: collapse;
  width: 100%;
  margin: 2ex 0;
  border: 1px solid var(--col-border);
  font-size: 11pt;
}
#details-table-basic td,
#details-table-details td {
  padding: 5px 8px 5px 8px;
}
#details-table-basic tr:nth-child(odd) > td.details-table-value,
#details-table-details tr:nth-child(odd) > td.details-table-value {
  background-color: var(--col-table-odd-value);
}
#details-table-basic tr:nth-child(even) > td.details-table-value,
#details-table-details tr:nth-child(even) > td.details-table-value {
  background-color: var(--col-table-even-value);
}
#details-table-basic colgroup,
#details-table-details colgroup {
  width: 50%;
  border: 1px dotted var(--col-border);
}
#details-table-basic .details-table-title,
#details-table-details .details-table-title {
  width: 140px;
  min-width: 140px;
  background-color: var(--col-table-head);
  text-align: left;
  font-weight: bold;
}
#details-table-basic .details-table-value,
#details-table-details .details-table-value {
  min-width: 400px;
  border: 1px solid var(--col-border);
  text-align: left;
}

.specitem-id {
  font-size: 11pt;
  max-width: 40vw;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  display: inline-block;
  vertical-align: middle;
}

.wrong-link-entry {
  display: inline-flex;
  align-items: center;
  gap: 4px;
}
.wrong-link-entry .wrong-link-copy-btn {
  vertical-align: middle;
}

