#tree-view {
  overflow: auto;
  max-height: 100%;
  min-width: 280px;
  max-width: 360px;
  word-wrap: break-word;
  overflow-wrap: break-word;
}
#tree-view .tree-list {
  list-style: none;
  margin: 0;
  padding: 0;
}
#tree-view .tree-node {
  margin: 0;
  padding: 0;
}
#tree-view .tree-node .tree-node-disabled {
  color: var(--col-disabled);
}
#tree-view .tree-node-label {
  display: flex;
  align-items: center;
  padding: 6px 8px;
  cursor: pointer;
  border-radius: 4px;
  transition: background-color 0.15s ease;
  user-select: none;
}
#tree-view .tree-node-label:hover {
  background-color: var(--col-hover);
}
#tree-view .tree-node-label.tree-node-selected {
  background-color: var(--col-selected);
  font-weight: 600;
}
#tree-view .tree-expand-icon {
  width: 16px;
  min-width: 16px;
  font-size: 10px;
  margin-right: 4px;
  display: inline-block;
  text-align: center;
  color: var(--col-text);
  transition: transform 0.2s ease;
}
#tree-view .tree-expand-icon.tree-folder-icon {
  width: 20px;
  min-width: 20px;
  height: 16px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
#tree-view .tree-expand-icon.tree-folder-icon.folder-closed {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_24___});
}
#tree-view .tree-expand-icon.tree-folder-icon.folder-open {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_25___});
}
#tree-view .tree-expand-icon-placeholder {
  width: 16px;
  min-width: 16px;
  display: inline-block;
  margin-right: 4px;
}
#tree-view .tree-node-name {
  flex: 0 1 auto;
  min-width: 0;
  font-weight: 500;
  color: var(--col-text);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
#tree-view .tree-node-count {
  font-size: 11px;
  color: var(--col-count);
  margin-left: 4px;
  flex-shrink: 0;
}
#tree-view .tree-children {
  margin-left: 10px;
}
#tree-view .tree-items {
  list-style: none;
  margin: 0 0 0 20px;
  padding: 0;
}
#tree-view .tree-item {
  padding: 4px 8px;
  cursor: pointer;
  border-radius: 4px;
  transition: background-color 0.15s ease;
}
#tree-view .tree-item:hover {
  background-color: var(--col-hover);
}
#tree-view .tree-item-title {
  font-size: 13px;
  color: var(--col-text);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: block;
}
#tree-view .tree-node[data-level="0"] > .tree-node-label {
  padding-left: 8px;
}
#tree-view .tree-node[data-level="0"] > .tree-node-label .tree-expand-icon {
  color: #2563eb;
}
#tree-view .tree-node[data-level="0"] > .tree-node-label .tree-node-name {
  color: #2563eb;
  font-weight: 600;
  max-width: 200px;
}
#tree-view .tree-node[data-level="0"].tree-node-disabled > .tree-node-label > .tree-node-name {
  color: var(--col-disabled) !important;
}
#tree-view .tree-node[data-level="1"] > .tree-node-label {
  padding-left: 8px;
}
#tree-view .tree-node[data-level="1"] > .tree-node-label .tree-node-name {
  max-width: 260px;
}
#tree-view .tree-node[data-level="2"] > .tree-node-label {
  padding-left: 8px;
}
#tree-view .tree-node[data-level="2"] > .tree-node-label .tree-node-name {
  max-width: 210px;
}
#tree-view .tree-node[data-level="3"] > .tree-node-label {
  padding-left: 8px;
}
#tree-view .tree-node[data-level="3"] > .tree-node-label .tree-node-name {
  max-width: 190px;
}
#tree-view .tree-node[data-level="4"] > .tree-node-label {
  padding-left: 8px;
}
#tree-view .tree-node[data-level="4"] > .tree-node-label .tree-node-name {
  max-width: 170px;
}

body.dark-mode #tree-view .tree-node[data-level="0"] > .tree-node-label .tree-node-name {
  color: #60a5fa;
}

#tree-hide-empty.nav-btn-on {
  background-color: var(--col-nav-active);
  border-radius: 6px;
